                  <Collapsible tabIndex="0" trigger="Specifies the configuration information associated with the provision of identity information to the resource server">
                    <p>Specifies the configuration information associated with the provision of identity information to the resource server.<br/><br/><h4> Encoding</h4><br/><table style={{textAlign:'left', border:'1px solid black'}}><tbody><tr style={{textAlign:'left', border:'1px solid black'}}><th style={{borderRight:'1px solid black', padding: '10px'}}>Type </th><th style={{borderRight:'1px solid black', padding: '10px'}}> Description</th></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>utf8_bin </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Unencoded UTF-8 data. This setting allows data to be transmitted without data loss, and the user does not need to URI-decode the data.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>utf8_uri </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> URI encoded UTF-8 data. All white space and non-ASCII bytes are encoded %XY, where X and Y are hex values (0-F).</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>lcp_bin </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> Unencoded local code page data. Data loss can potentially occur with this mode. Use with caution.</td></tr><tr><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}>lcp_uri </td><td style={{borderRight:'1px solid black', paddingLeft: '10px'}}> URI encoded local code page data. Any UTF-8 characters that cannot be converted to a local code page are converted to question marks (?).</td></tr></tbody></table><br/><h4> Basic Authentication Modes</h4><br/>Mode    | Description<br/>---- ---| -----------<br/>filter  | The Authorization header, if provided by the client, will be removed before the resource request is forwarded to the resource server.  This is the default option if no option is specified.<br/>supply  | A new Authorization header will be created by the gateway and forwarded to the resource server.  The Basic Authentication information contained in the header will consist of the name of the authenticated user, along with a static password.  Use the `password` YAML configuration entry to set the static password.<br/>ignore  | The Authorization header, if provided by the client, will be forwarded to the resource server unchanged.<br/>service | A new Authorization header will be constructed using a username and password retrieved from a configured credential service. See "services/credential" for more information about configuring a credential service. The YAML entries "service" and "resource_name" must be specified when using this mode.</p>
                  </Collapsible>

